// MyAccountComponent.js
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import styles from './styles.module.css';
import { toastPending, toastUpdateError, toastUpdateSuccess } from '../../../helpers/toast';

function MyAccountComponent() {
  const [carList, setCarList] = useState([]);

  const formatCurrency = (value) => {
    const cleanValue = value.replace(/[^\d]/g, '');
    const formattedValue = Number(cleanValue).toLocaleString('vi-VN');
    return formattedValue;
  };

  useEffect(() => {

    const token = localStorage.getItem('token');
    axios.defaults.headers.common['Authorization'] = `${token}`;

    axios.get('/mycars')
      .then(response => {
        setCarList(response.data);
      })
      .catch(error => {
        console.error('Error:', error);
      });
  }, []);

  const handleDeleteCar = (carId) => {
    const loading = toastPending('ang xo xe')
    const isConfirmed = window.confirm('Xo tht nh?');
    if (isConfirmed) {
      axios.delete(`/cars/delete/${carId}`)
        .then(response => {
          setCarList(carList.filter(car => car.car_id !== carId));
          toastUpdateSuccess(loading, 'Xo thnh cng')
        })
        .catch(error => {
          console.error('Error:', error);
          toastUpdateError(loading, 'Xo tht bi')
        });
    } else {
      toastUpdateError(loading, 'T chi xo')
    }
  };

  return (
    <>
      <div className={styles.buttonGroupCreate}>
        <Link to="/create" className={styles.createButton}>Thm mi</Link>
      </div>
      <div className={`${styles.tableResponsive}`}>
        <table border={1} className={styles.carTable}>
          <thead>
            <tr>
              <th>Tn xe</th>
              <th>Danh mc</th>
              <th>Thng hiu</th>
              <th>Mu xe</th>
              <th>Nm sn xut</th>
              <th>Gi xe</th>
              <th>Trng thi</th>
              <th>Hnh ng</th>
            </tr>
          </thead>
          <tbody>
            {carList.map(car => (
              <tr
                key={car.id}
                className={`${styles.carItem} ${car.status === 0 ? styles.pending : car.status === 1 ? styles.actived : styles.reject}`}
              >
                <td>{car.car_name}</td>
                <td>{car.cate_name}</td>
                <td>{car.brand_name}</td>
                <td>{car.model}</td>
                <td>{car.year}</td>
                <td>{formatCurrency(car.price)}</td>
                <td>{car.status === 0 ? 'Ch duyt' : car.status === 1 ? 'ang hot ng' : 'T chi hin th'}</td>
                <td>
                  <div className={styles.buttonGroup}>
                    <Link to={`/edit/${car.car_id}`} className={styles.editButton}>Sa</Link>
                    <button onClick={() => handleDeleteCar(car.car_id)} className={styles.deleteButton}>Xo</button>
                  </div>
                </td>
              </tr>
            ))}
            {
              carList.length === 0 && (
                <tr className={`${styles.carItem} ${styles.noData}`}>
                  <td colSpan={8} style={{ textAlign: 'center' }}>
                    Khng c d liu
                  </td>
                </tr>
              )
            }
          </tbody>
        </table>
      </div>
    </>
  );
}

export default MyAccountComponent;
