import React, { useEffect, useState } from 'react';
import axios from 'axios';
import styles from './styles.module.css';
import { toastPending, toastUpdateError, toastUpdateSuccess } from '../helpers/toast';

const ContactForm = () => {
  const [contactInfo, setContactInfo] = useState({
    garage_id: '',
    full_name: '',
    gender: '',
    price_range: '',
    phone: '',
  });
  const [error, setError] = useState({});

  const handleChange = (e) => {
    const { name, value } = e.target;
    setContactInfo(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  const handleCreateContact = async () => {
    setError(prevState => (
      {
        ...prevState,
        garage_id: !contactInfo.garage_id?.trim() ? 'Garage l bt buc' : '',
        full_name: !contactInfo.full_name?.trim() ? 'Tn l bt buc' : '',
        gender: !contactInfo.gender?.trim() ? 'Gii tnh l bt buc' : '',
        price_range: !contactInfo.price_range?.trim() ? 'Khong gi l bt buc' : '',
        phone: !contactInfo.phone?.trim() ? 'S in thoi l bt buc' : '',
      }
    ));

    if (
      !contactInfo.garage_id?.trim() ||
      !contactInfo.full_name?.trim() ||
      !contactInfo.gender?.trim() ||
      !contactInfo.price_range?.trim() ||
      !contactInfo.phone?.trim()
    ) {
      return;
    }

    const loading = toastPending('ang ng k')
    try {
      await axios.post('/contacts', contactInfo).then((res) => {
        toastUpdateSuccess(loading, 'ng k thnh cng')
        setContactInfo({
          garage_id: '',
          full_name: '',
          gender: '',
          price_range: '',
          phone: ''
        })
      });
      // Thc hin cc hnh ng sau khi to contact thnh cng (VD: hin th thng bo, redirect...)
    } catch (error) {
      console.error('Error creating contact:', error);
      toastUpdateError(loading, 'ng k tht bi')
      // X l li (VD: hin th thng bo li)
    }
  }

  const [userList, setUserList] = useState([]);

  useEffect(() => {
    getUsers();
  }, []);

  const getUsers = async () => {
    try {
      const token = localStorage.getItem('token');
      axios.defaults.headers.common['Authorization'] = `${token}`;

      const response = await axios.get('/users-exa');
      setUserList(response.data);
    } catch (error) {
      console.error('Error fetching users:', error);
    }
  };

  return (
    <div className={styles.wrapper}>
      <div style={{ width: '100%' }}>
        <div className={styles.container}>
          <table border={0} className={styles.carTable}>
            <tbody>
              <tr>
                <td colSpan={2}>
                  <h2 className={styles.title}>ng k t vn</h2>
                </td>
              </tr>
              <tr>
                <td>
                  <label htmlFor="selectedUser">Chn Showroom</label>
                </td>
                <td>
                  {error.garage_id && <div className='error'>{error.garage_id}</div>}
                  <select id="selectedUser" name="garage_id" value={contactInfo.garage_id} onChange={handleChange} className={styles.select}>
                    <option value="">-- Showroom --</option>
                    {userList.map(user => (
                      <option key={user.user_id} value={user.user_id}>{user.first_name} {user.last_name}</option>
                    ))}
                  </select>
                </td>
              </tr>
              <tr>
                <td>
                  <label htmlFor="fullName">Tn qu khch:</label>
                  </td>
                <td>
                  {error.full_name && <div className='error'>{error.full_name}</div>}
                  <input type="text" id="fullName" name="full_name" value={contactInfo.full_name} onChange={handleChange} className={styles.input} />
                </td>
              </tr>
              <tr>
                <td><label htmlFor="gender">Gii tnh:</label></td>
                <td>
                  {error.gender && <div className='error'>{error.gender}</div>}
                  <select id="gender" name="gender" value={contactInfo.gender} onChange={handleChange} className={styles.select}>
                    <option value="">-- Gii Tnh --</option>
                    <option value="male">Nam</option>
                    <option value="female">N</option>
                    <option value="other">Khc</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td><label htmlFor="priceRange">Gi khong:</label></td>
                <td>
                  {error.price_range && <div className='error'>{error.price_range}</div>}
                  <input type="text" id="priceRange" name="price_range" value={contactInfo.price_range} onChange={handleChange} className={styles.input} />
                </td>
              </tr>
              <tr>
                <td><label htmlFor="phone">Phone:</label></td>
                <td>
                  {error.phone && <div className='error'>{error.phone}</div>}
                  <input type="phone" id="phone" name="phone" value={contactInfo.phone} onChange={handleChange} className={styles.input} />
                </td>
              </tr>
              <tr>
                <td colSpan={2}>
                  <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
                    <button onClick={handleCreateContact} className={styles.createButton}>Gi</button>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default ContactForm;
