import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link, useHistory } from 'react-router-dom'
import styles from './styles.module.css'
import { toastPending, toastUpdateError, toastUpdateSuccess } from '../helpers/toast';

const UpdateUserInfo = () => {
  const history = useHistory()
  const [userInfo, setUserInfo] = useState({
    username: '',
    phone: '',
    avatar: '',
    first_name: '',
    last_name: '',
    password: '',
  });
  const [error, setError] = useState({});

  useEffect(() => {
    // Gi API  ly thng tin ngi dng hin ti
    const fetchUserInfo = async () => {
      try {
        const token = localStorage.getItem('token');
        axios.defaults.headers.common['Authorization'] = `${token}`;

        const response = await axios.get('/myaccount');
        setUserInfo({
          ...response.data,
          password: ''
        });
      } catch (error) {
        console.error('Error fetching user info:', error);
      }
    };

    fetchUserInfo();
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setUserInfo(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {

    setError(prevState => (
      {
        ...prevState,
        first_name: !userInfo.first_name?.trim() ? 'H l bt buc' : '',
        last_name: !userInfo.last_name?.trim() ? 'Tn l bt buc' : '',
        phone: !userInfo.phone?.trim() ? 'S in thoi l bt buc' : '',
      }
    ));

    if (
      !userInfo.first_name?.trim() ||
      !userInfo.last_name?.trim() ||
      !userInfo.phone?.trim()
    ) {
      return;
    }

    const loading = toastPending('ang cp nht')
    try {
      const token = localStorage.getItem('token');
      axios.defaults.headers.common['Authorization'] = `${token}`;

      await axios.put('/updateUserInfo', {
        phone: userInfo.phone,
        avatar: userInfo.avatar,
        first_name: userInfo.first_name,
        last_name: userInfo.last_name,
        ...userInfo.password && userInfo.password !== '' ? { password: userInfo.password} : {},
      }).then((res) => {
        history.push('/myaccount')
        toastUpdateSuccess(loading, 'Cp nht thnh cng')
      });
      // X l thnh cng
    } catch (error) {
      console.log('Error updating user info:', error);
      toastUpdateError(loading, 'Cp nht tht bi')
    }
  };

  return (
    <>
      <div className={styles.wrapper}>
        <div style={{ width: '100%' }}>
          <div className={styles.container}>
            <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
              <Link to="/myaccount" className={styles.backButton}>Quay li</Link>
            </div>
            <table border={0} className={styles.carTable}>
              <tbody>
                <tr>
                  <td colSpan={2}>
                    <h2 className={styles.title}>Cp nht thng tin</h2>
                  </td>
                </tr>
                <tr>
                  <td>
                    <label>Username</label>
                  </td>
                  <td>
                    <input disabled type="text" name="username" value={userInfo.username} className={styles.input} />
                  </td>
                </tr>
                <tr>
                  <td>
                    <label>H</label>
                  </td>
                  <td>
                    {error.first_name && <div className='error'>{error.first_name}</div>}
                    <input type="text" name="first_name" onChange={handleChange} value={userInfo.first_name} className={styles.input} />
                  </td>
                </tr>
                <tr>
                  <td>
                    <label>Tn</label>
                  </td>
                  <td>
                    {error.last_name && <div className='error'>{error.last_name}</div>}
                    <input type="text" name="last_name" onChange={handleChange} value={userInfo.last_name} className={styles.input} />
                  </td>
                </tr>
                <tr>
                  <td>
                    <label>S in thoi</label>
                  </td>
                  <td>
                    {error.phone && <div className='error'>{error.phone}</div>}
                    <input type="text" name="phone" onChange={handleChange} value={userInfo.phone} className={styles.input} />
                  </td>
                </tr>
                <tr>
                  <td>
                    <label>nh i din</label>
                  </td>
                  <td>
                    {error.avatar && <div className='error'>{error.avatar}</div>}
                    <input type="text" name="avatar" onChange={handleChange} value={userInfo.avatar} className={styles.input} />
                  </td>
                </tr>
                <tr>
                  <td>
                    <label>Mt khu</label>
                  </td>
                  <td>
                    <input type="password" name="password" onChange={handleChange} value={userInfo.password} className={styles.input} />
                  </td>
                </tr>
                <tr>
                  <td colSpan={2}>
                    <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
                      <button onClick={handleSubmit} className={styles.createButton}>Cp nht</button>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
};

export default UpdateUserInfo;
