// CarFormComponent.js
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Link, useHistory } from 'react-router-dom';
import styles from './styles.module.css';
import { toastPending, toastUpdateError, toastUpdateSuccess } from '../helpers/toast';

function CarFormComponent() {
  const [image, setImage] = useState('');
  const [carName, setCarName] = useState('');
  const [brandId, setBrandId] = useState('');
  const [cateId, setCateId] = useState('');
  const [model, setModel] = useState('');
  const [year, setYear] = useState('');
  const [price, setPrice] = useState('');
  const [carDescription, setCarDesription] = useState('');
  const [carBrands, setCarBrands] = useState([]);
  const [categories, setCategories] = useState([]);
  const history = useHistory();
  const [error, setError] = useState({});

  const formatCurrency = (value) => {
    const cleanValue = value.replace(/[^\d]/g, ''); // Loi b k t khng phi s
    const formattedValue = Number(cleanValue).toLocaleString('vi-VN'); // nh dng s theo tiu chun Vit Nam
    return formattedValue;
  };

  useEffect(() => {
    axios.get('/carBrands')
      .then(response => {
        setCarBrands(response.data);
      })
      .catch(error => {
        console.error('Error:', error);
      });

    axios.get('/categories')
      .then(response => {
        setCategories(response.data);
      })
      .catch(error => {
        console.error('Error:', error);
      });
  }, []);

  const handleCreateCar = () => {

    setError(prevState => (
      {
        ...prevState,
        image: !image?.trim() ? 'Hnh nh l bt buc' : '',
        carName: !carName?.trim() ? 'Tn xe l bt buc' : '',
        brandId: !brandId?.trim() ? 'Thng hiu l bt buc' : '',
        model: !model?.trim() ? 'Mu xe l bt buc' : '',
        year: !year?.trim() ? 'Nm sn xut l bt buc' : '',
        price: !price?.trim() ? 'Gi l bt buc' : '',
        cateId: !cateId?.trim() ? 'Danh mc l bt buc' : '',
      }
    ));

    if (
      !image?.trim() ||
      !carName?.trim() ||
      !brandId?.trim() ||
      !model?.trim() ||
      !year?.trim() ||
      !price?.trim() ||
      !cateId?.trim()
    ) {
      return;
    }

    const loading = toastPending('ang to')
    axios.post('/cars/create', { car_name: carName, brand_id: brandId, cate_id: cateId, model, image, year, price: price.replace(/[.,]/g, ""), car_description: JSON.stringify(carDescription) })
      .then(response => {
        toastUpdateSuccess(loading, 'To thnh cng')
        history.push('/myaccount')
      })
      .catch(error => {
        toastUpdateError(loading, 'To tht bi')
        console.error('Error:', error);
        // Hin th thng bo li
      });
  };

  return (
    <>
      <div className={styles.wrapper}>
        <div style={{ width: '100%' }}>
          <div className={styles.container}>
            <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
              <Link to="/myaccount" className={styles.backButton}>Quay li</Link>
            </div>
            <table border={0} className={styles.carTable}>
              <tbody>
                <tr>
                  <td colSpan={2}>
                    <h2 className={styles.title}>To mi xe</h2>
                  </td>
                </tr>
                <tr>
                  <td>
                    <label>Link nh xe</label>
                  </td>
                  <td>

                    {error.image && <div className='error'>{error.image}</div>}
                    <input type="text" placeholder="Car link image" value={image} onChange={(e) => setImage(e.target.value)} className={styles.input} />
                  </td>
                </tr>

                <tr>
                  <td>
                    <label>Tn xe</label>
                  </td>
                  <td>
                    {error.carName && <div className='error'>{error.carName}</div>}
                    <input type="text" placeholder="Car name" value={carName} onChange={(e) => setCarName(e.target.value)} className={styles.input} />
                  </td>
                </tr>

                <tr>
                  <td>
                    <label>Danh mc</label>
                  </td>
                  <td>
                    {error.cateId && <div className='error'>{error.cateId}</div>}
                    <select value={cateId} onChange={(e) => setCateId(e.target.value)} className={styles.select}>
                      <option value="">Danh mc</option>
                      {categories.map(cate => (
                        <option key={cate.cate_id} value={cate.cate_id}>{cate.cate_name}</option>
                      ))}
                    </select>
                  </td>
                </tr>

                <tr>
                  <td>
                    <label>Thng hiu</label>
                  </td>
                  <td>
                    {error.brandId && <div className='error'>{error.brandId}</div>}
                    <select value={brandId} onChange={(e) => setBrandId(e.target.value)} className={styles.select}>
                      <option value="">Thng hiu</option>
                      {carBrands.map(brand => (
                        <option key={brand.brand_id} value={brand.brand_id}>{brand.brand_name}</option>
                      ))}
                    </select>
                  </td>
                </tr>

                <tr>
                  <td>
                    <label>Mu xe</label>
                  </td>
                  <td>
                    {error.model && <div className='error'>{error.model}</div>}
                    <input type="text" placeholder="Model" value={model} onChange={(e) => setModel(e.target.value)} className={styles.input} />
                  </td>
                </tr>

                <tr>
                  <td>
                    <label>Nm sn xut</label>
                  </td>
                  <td>
                    {error.year && <div className='error'>{error.year}</div>}
                    <input type="number" placeholder="Year" value={year} onChange={(e) => setYear(e.target.value)} className={styles.input} />
                  </td>
                </tr>

                <tr>
                  <td>
                    <label>Gi xe</label>
                  </td>
                  <td>
                    {error.price && <div className='error'>{error.price}</div>}
                    <input type="text" placeholder="Price" value={price} onChange={(e) => setPrice(formatCurrency(e.target.value))} className={styles.input} />
                  </td>
                </tr>

                <tr>
                  <td>
                    <label>M t</label>
                  </td>
                  <td>
                    <textarea
                      style={{ resize: 'vertical' }}
                      placeholder="M t"
                      value={carDescription}
                      onChange={(e) => setCarDesription(e.target.value)}
                      className={styles.input}
                    />
                  </td>
                </tr>
                <tr>
                  <td colSpan={2}>
                    <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
                      <button onClick={handleCreateCar} className={styles.createButton}>To</button>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
}

export default CarFormComponent;
