// MyAccountComponent.js
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import styles from './styles.module.css';
import { toastPending, toastUpdateError, toastUpdateSuccess } from '../../../helpers/toast';

function MyAccountComponent() {
  const [contactList, setContactList] = useState([]);

  useEffect(() => {

    const token = localStorage.getItem('token');
    axios.defaults.headers.common['Authorization'] = `${token}`;

    axios.get('/contacts')
      .then(response => {
        setContactList(response.data);
      })
      .catch(error => {
        console.error('Error:', error);
      });
  }, []);

  const handleDeleteContact = (contactId) => {
    const loading = toastPending('ang xo yu cu t vn')
    const isConfirmed = window.confirm('Xo tht nh?');
    if (isConfirmed) {
      axios.delete(`/contacts/${contactId}`)
        .then(response => {
          setContactList(contactList.filter(contact => contact.contact_id !== contactId));
          toastUpdateSuccess(loading, 'Xo thnh cng')
        })
        .catch(error => {
          console.error('Error:', error);
          toastUpdateError(loading, 'Xo tht bi')
        });
    } else {
      toastUpdateError(loading, 'T chi xo')
    }
  };

  return (
    <>
      <table border={1} className={styles.carTable}>
        <thead>
          <tr>
            <th>H v Tn</th>
            <th>Gii tnh</th>
            <th>Khong gi</th>
            <th>S in thoi</th>
            <th>Hnh ng</th>
          </tr>
        </thead>
        <tbody>
          {contactList.map(contact => (
            <tr key={contact.contact_id} className={styles.carItem}>
              <td>{contact.full_name}</td>
              <td>{contact.gender === 'Male' ? 'Nam' : contact.gender === 'Femal' ? 'N' : 'Khc'}</td>
              <td>{contact.price_range}</td>
              <td>(+84) {Number(contact.phone).toLocaleString('it-IT')}</td>
              <td>
                <div className={styles.buttonGroup}>
                  <button onClick={() => handleDeleteContact(contact.contact_id)} className={styles.deleteButton}>Xo</button>
                </div>
              </td>
            </tr>
          ))}
          {
            contactList.length === 0 && (
              <tr className={`${styles.carItem} ${styles.noData}`}>
                <td colSpan={5} style={{ textAlign: 'center' }}>
                  Khng c d liu
                </td>
              </tr>
            )
          }
        </tbody>
      </table>
    </>
  );
}

export default MyAccountComponent;
